/* Default
----------------------------------------------------------*/

* {
    margin: 0;
    padding: 0;
    outline: medium none;
}

img {
    border: none;
    vertical-align: middle;
}

ul.l {
    list-style: none;
    padding: 0;
    margin: 0;
}

ul.l a,
a {
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

body {
    background-color: #ffffff;
    font-family: Tahoma, Arial, Myriad Pro;
    font-size: 12px;
    line-height: 21px;
}

body input {
    font: 12px Arial;
}

.cl {
    clear: both;
}

.space {
    background-color: #ffffff;
    width: 100%;
    clear: both;
    height: 2px;
}

.border-line {
    background-color: #b7b7b7;
    width: 100%;
    clear: both;
    height: 1px;
}

/* Header
----------------------------------------------------------*/

header {
    width: 100%;
    height: 185px;
    background-color: #29aae2;
}

#w-header {
    background: url("/public/Images/header-bg.png") repeat-x scroll center center transparent;
    height: 185px;
    margin: 0 auto;
    width: 1203px;
    position: relative;
    z-index: 99;
}

/* Header Top
----------------------------------------------------------*/

#w-header .w-h-top {
    width: 100%;
    padding: 4px 0 0;
    height: 24px;
    float: right;
}

#w-header .w-h-top .w-h-top-h3 {
    color: #fff;
    float: left;
    font-size: 12px;
    font-weight: normal;
    padding-left: 10px;
    text-transform: uppercase;
    width: 200px;
}

#w-header .w-h-top ul {
    float: right;
    padding-right: 17px;
}

#w-header .w-h-top ul li:last-child {
    background: none;
}

#w-header .w-h-top ul li {
    float: left;
    padding: 0 9px 0 7px;
    color: #ffffff;
    background: url("/public/Images/m-top-border.png") no-repeat scroll right 6px transparent;
}

#w-header .w-h-top ul li a {
    font: 12px/24px Tahoma, Arial;
    color: #ffffff;
}

/* Header Left
----------------------------------------------------------*/

#w-header .w-h-left {
    float: left;
    width: 198px;
    height: 109px;
}

#w-header .w-h-left a {
    background: url("/public/Images/logo.PNG") no-repeat scroll center transparent;
    cursor: pointer;
    display: block;
    height: 90px;
    width: 176px;
    margin: 10px auto 0;
    background-size: contain;
}

/* Header Center
----------------------------------------------------------*/

#w-header .w-h-center {
    float: left;
    height: 109px;
    width: 415px;
}

#w-header .w-h-center .w-h-c-search {
    background-color: #ffffff;
    border-radius: 2px;
    height: 30px;
    margin: 47px 0 0 45px;
    position: relative;
    width: 360px;
    padding: 3px;
}

#w-header .w-h-center .w-h-c-search input[type="text"] {
    background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
    border: medium none;
    float: left;
    font: 12px/30px Arial;
    height: 30px;
    width: 270px;
    padding: 0 10px;
}

#w-header .w-h-center .w-h-c-search input[type="submit"] {
    background: transparent url("/public/Images/btn-search.png") no-repeat scroll center;
    border: medium none;
    color: #ffffff;
    cursor: pointer;
    float: right;
    font: 12px Tahoma;
    height: 30px;
    width: 66px;
}

/* Header Center
----------------------------------------------------------*/

#w-header .w-h-right {
    float: right;
    height: 72px;
    width: 565px;
    padding: 37px 0 0;
}

#w-header .wh-call {
    float: left;
    display: block;
    background: url("/public/Images/support-phone.png") no-repeat scroll -16px top transparent;
    width: 550px;
    height: 51px;
    padding-left: 120px;
    padding-top: 13px;
}

#w-header .wh-call {}

#w-header .wh-call a {
    color: #fff200;
    display: block;
    text-align: right;
    font: bold 20px Times New Roman;
