    text-shadow: 1px 0 1px #0066C9, -1px 0 1px #0066C9, 0 1px 1px #0066C9, 0 -2px 1px #0066C9, 1px 2px 1px #0066C9, -2px -2px 1px #0066C9, 2px 0px 1px #0066C9;
