#w-header .w-h-right ul li a.icon-cart {
    display: block;
    /* background: url("images/icon-cart.png") no-repeat scroll top left transparent; */
    margin-left: 15px;
    width: 47px;
    height: 49px;
    position: relative;
    z-index: 98;
}

#w-header .w-h-right ul li a.icon-cart span {
    color: #ffffff;
    font: 11px/12px Tahoma, Arial;
    position: absolute;
    right: 1px;
    text-align: center;
    top: 0;
