/* wf-fixcall
----------------------------------------------------------*/

.wf-fixcall {
    background: #145c92;
    height: 34px;
    -webkit-transition: all ease-in-out .2s;
    -moz-transition: all ease-in-out .2s;
    -o-transition: all ease-in-out .2s;
    transition: all ease-in-out .2s;
    bottom: 0;
    position: fixed;
    left: 0;
    width: 100%;
    z-index: 99;
    padding: 6px 0;
}

.wf-fixcall ul {
    width: 1163px;
    height: 100%;
    margin: 0 auto;
    text-shadow: 1px 1px 1px #000;
    /* background: url("images2021/call-icon.png") no-repeat scroll 0 top transparent; */
    padding-left: 40px;
}

.wf-fixcall ul li {
    display: inline-block;
    color: #FFF;
    padding-right: 5px;
    font-size: 14px;
    line-height: 36px;
    vertical-align: top;
}

.wf-fixcall ul li a {
    font-family: brandon_textregular;
    color: #FFF;
    font-size: 24px;
    line-height: 32px;
}

