/* Pagging
----------------------------------------------------------*/

ul#pagination {
    margin: 14px 0;
    white-space: nowrap;
}

ul#pagination>li {
    display: inline;
}

ul#pagination>li>a,
ul#pagination>li>span {
    background-color: #fff;
    border: 1px solid #ddd;
    color: #428bca;
    float: left;
    line-height: 1.42857;
    margin-left: -1px;
    padding: 6px 12px;
    position: relative;
    text-decoration: none;
}

ul#pagination>li:first-child>a,
ul#pagination>li:first-child>span {
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    margin-left: 0;
}

ul#pagination>li:last-child>a,
ul#pagination>li:last-child>span {
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
}

ul#pagination>.active>a,
ul#pagination>.active>span,
ul#pagination>.active>a:hover,
ul#pagination>.active>span:hover,
ul#pagination>.active>a:focus,
ul#pagination>.active>span:focus {
    background-color: #428bca;
    border-color: #428bca;
    color: #fff;
    cursor: default;
    z-index: 2;
}

/* Support Online 
----------------------------------------------------------*/

.support-online {
    font: 13px/21px Arial;
    height: 450px;
}

.support-online span {
    color: #ff0000;
    font-weight: bold;
}

.support-online table {
    border-collapse: collapse;
    width: 100%;
    margin: 7px 0;
}

.support-online table,
.support-online table tr,
.support-online table tr td {
    border: 1px solid #dfdfdf;
    padding: 7px;
}

/* Box Promotion
----------------------------------------------------------*/

.box-promotion {
    /* background: url("images-promotion/box_overlay.png") repeat scroll 0 0 transparent; */
    display: none;
    left: 0;
    overflow: hidden;
    position: absolute;
    top: 0;
    z-index: 99;
}

.box-promotion-active {
    display: block;
    vertical-align: middle;
    bottom: 0;
    position: fixed;
    right: 0;
}

.box-promotion .box-promotion-item {
    height: 342px;
    left: 427px;
    position: absolute;
    top: 137px;
    width: 500px;
}

.box-promotion .box-promotion-item .box-banner {
    position: absolute;
    z-index: 97;
}

.box-promotion .box-promotion-item a.box-promotion-close {
    /* background: url("images-promotion/delete-large.png") no-repeat scroll 0 0 transparent; */
    position: absolute;
    z-index: 98;
    right: -29px;
    cursor: pointer;
    top: -13px;
    width: 72px;
    height: 72px;
}

